.banner{
    background-size: cover;
    height: 448px;
    color: white;
    cursor: pointer;
}
.content{
    padding-top: 140px;
    height: 190px;
    padding-left: 15px;

}
.title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.button{

    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgb( 51, 51, 51,0.5);
    cursor: pointer;
    margin: 1rem;
}

.button:hover{
    color: black;
    background-color:#e6e6e6 ;
}

.description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    height: 80px;
    max-width: 800px;
}

.fadebottom{
    height: 7.4rem;
    background-image: -webkit-gradient(linear,left top, left bottom,from(transparent),color-stop(rgba(37,37,37,0.61)),to(#111));
    background-image: -webkit-linear-gradient(top,transparent,rgba(37,37,37,0.61),#111);
    background-image: -o-linear-gradient(top,transparent,rgba(37,37,37,0.61),#111);
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,0.61),#111);
}





